      type: Object,
      default: () => {

      }
    },
    /**  */
    autoDataMethods: { 
      type: Function
    },
    /**  */
    tableDataMethods: { 
      type: Function
    }
  },
  methods: {
    render (html) {
      this.html = html;
    }
